import { Table } from "antd";
import moment from "moment";
import React from "react";
import Styled from "./Styled";

const DiplomaInfo = (props) => {
    const { employeeInfo } = props

    const columns = [
        { key: "name", title: "Tn vn bng", dataIndex: "name", width: "20%" },
        { key: "content", title: "Ni dung", dataIndex: "content", width: "22%" },
        { key: "educationalOrg", title: "Ni cp", dataIndex: "educationalOrg", width: "18%" },
        {
            key: "issuanceDate", title: "Ngy cp", dataIndex: "issuanceDate", width: "20%",
            render: (rowData) => {
                return (
                    <>{moment(rowData).format("DD-MM-YYYY")}</>
                )
            }
        },
        { key: "field", title: "Lnh vc", dataIndex: "field", width: "20%" },
    ];

    return (
        <Styled>
            <Table
                dataSource={employeeInfo?.certificates}
                columns={columns}
                pagination={false}
            ></Table>
        </Styled>
    )
}

export default DiplomaInfo;