import { Button, Form, Input, Modal } from 'antd';
import React from 'react';

function RegisterProfileForm({ open, onCancel, staffInfo, onSend }) {
    return (
        <Modal
            className="information_modal"
            centered
            title="Biu mu"
            open={open}
            onCancel={onCancel}
            width={'70vw'}
            footer={[
                <Button onClick={onCancel} key="1">
                    Hy
                </Button>,
                <Button type="primary" onClick={onSend} key="2">
                    Trnh lnh o
                </Button>,
                <Button className="btn-save" key="3">
                    Lu
                </Button>,
            ]}
        >
            <Form>
                <div
                    style={{
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        textAlign: 'center',
                    }}
                >
                    <h4 style={{ fontSize: 18 }}>CNG HA X HI CH NGHA VIT NAM</h4>
                    <h5>
                        <span style={{ borderBottom: '2px solid #666', paddingBottom: '3px' }}>
                            c lp - T do - Hnh Phc
                        </span>
                    </h5>
                    <h3 style={{ fontSize: 20, marginTop: '30px' }}>NG K H S</h3>
                </div>

                <div style={{ marginTop: '50px' }}>
                    <p>Knh gi ban gim c cng ty OceanTech</p>
                    <div>
                        <Form.Item name="fullName" label="1. Ti tn l: ">
                            <Input />
                        </Form.Item>
                    </div>
                    <div>
                        <Form.Item name="profileName" label="2. Tn h s: ">
                            <Input />
                        </Form.Item>
                    </div>
                    <div>
                        <Form.Item name="dateRegister" label="3. Ngy ng k: ">
                            <Input />
                        </Form.Item>
                    </div>
                    <div>
                        <Form.Item name="note" label="4. Ghi ch: ">
                            <Input />
                        </Form.Item>
                    </div>
                    <div>
                        <Form.Item name="content" label="5. Ni dung: ">
                            <Input />
                        </Form.Item>
                    </div>
                    <p>Knh mong Cng ty / c nhn c thm quyn xem xt h s v xt duyt h s cho ti.</p>
                    <p>
                        Ti xin cam oan nhng thng tin trn hon ton ng s tht, nu c g sai st ti xin chu
                        trch nhim trc Cng ty v trc php lut.
                    </p>
                    <p>Ti xin chn thnh cm n!</p>
                </div>

                <div>
                    <div className="modal_signature">
                        <div
                            className="date-signature"
                            style={{ display: 'flex', alignItems: 'center', marginBottom: '15px' }}
                        >
                            H ni,
                            <Form.Item name="day">
                                <Input className="day" />
                            </Form.Item>
                            ,
                            <Form.Item name="date">
                                <Input />
                            </Form.Item>
                            thng
                            <Form.Item name="month">
                                <Input />
                            </Form.Item>
                            ,
                            <Form.Item name="year">
                                <Input className="year" />
                            </Form.Item>
                        </div>
                        <p>Ngi khai</p>
                        <p>{staffInfo.fullName.split(' ').slice(-1).join(' ')}</p>
                        <p>{staffInfo.fullName}</p>
                    </div>
                </div>
            </Form>
        </Modal>
    );
}

export default RegisterProfileForm;
