import { Button, Form, Input, Modal } from 'antd';
import React from 'react';
import { useState } from 'react';

function ProposedForm({ open, onCancel, staffInfo, proposedRecord, onSend }) {
    const [dataForm, setDataForm] = useState({
        fullName: staffInfo?.fullName || '',
    });

    const onChangeValue = (e) => {
        setDataForm({ ...dataForm, [e.target.name]: e.target.defaultValue });
    };

    return (
        <Modal
            className="information_modal"
            centered
            title="Biu mu"
            open={open}
            onCancel={onCancel}
            width={'70vw'}
            footer={[
                <Button onClick={onCancel} key="1">
                    Hy
                </Button>,
                <Button type="primary" onClick={onSend} key="2">
                    Trnh lnh o
                </Button>,
                <Button key="3" className="btn-save">
                    Lu
                </Button>,
            ]}
        >
            <div style={{ display: 'flex', flexDirection: 'column', textAlign: 'center' }}>
                <h4 style={{ textTransform: 'uppercase', fontSize: '20px' }}>Cng HA X HI CH NGHA VIT NAM</h4>
                <p>
                    <span style={{ fontSize: '18px', borderBottom: '2px solid #4d4d4d', paddingBottom: '5px' }}>
                        c lp - T do - Hnh Phc
                    </span>
                </p>
            </div>
            <div className="modal_content">
                <h4
                    style={{
                        textTransform: 'uppercase',
                        textAlign: 'center',
                        padding: '30px 0',
                        fontSize: '18px',
                    }}
                >
                    N  XUT  KIN THAM MU
                </h4>
                <p>Knh gi: Ban Gim c cng ty OceanTech</p>

                <Form.Item name="name" onChange={onChangeValue} label="Ti tn l: ">
                    <Input />
                </Form.Item>
                <Form.Item name="location" onChange={onChangeValue} label="Hin ang cng tc ti v tr: ">
                    <Input />
                </Form.Item>
                <Form.Item
                    name="problem"
                    onChange={onChangeValue}
                    label="Ti vit n ny  ngh  Cng ty xem xt v gii quyt vn : "
                >
                    <Input />
                </Form.Item>
                <Form.Item name="date" onChange={onChangeValue} label="K t ngy: ">
                    <Input />
                </Form.Item>
                <div className="form_block">
                    <Form.Item name="reason" onChange={onChangeValue} label="Ni dung nh sau: ">
                        <Input />
                    </Form.Item>
                </div>

                <p>
                    Knh mong Cng ty / c nhn c thm quyn xem xt n  ngh v gii quyt vn  m ti  nu 
                    trn.
                </p>
                <p>
                    Ti xin cam oan nhng thng tin trn hon ton ng s tht, nu c g sai st ti xin chu trch
                    nhim trc Cng ty v trc php lut.
                </p>
                <p>Ti xin chn thnh cm n!</p>
            </div>
            <div className="modal_signature">
                <div className="date-signature" style={{ display: 'flex', alignItems: 'center', marginBottom: '15px' }}>
                    H ni,
                    <Form.Item name="day" onChange={onChangeValue}>
                        <Input className="day" />
                    </Form.Item>
                    ,
                    <Form.Item name="date" onChange={onChangeValue}>
                        <Input />
                    </Form.Item>
                    thng
                    <Form.Item name="month" onChange={onChangeValue}>
                        <Input />
                    </Form.Item>
                    ,
                    <Form.Item name="year" onChange={onChangeValue}>
                        <Input className="year" />
                    </Form.Item>
                </div>
                <p>Ngi lm n</p>
                <p>{staffInfo.fullName.split(' ').slice(-1).join(' ')}</p>
                <p>{staffInfo.fullName}</p>
            </div>
        </Modal>
    );
}

export default ProposedForm;
